.waitPlayerInner {
    width: 100%;
    height: 100%;
}
.waitPlayerInner  {
    margin-top: -30px;
    transition: 0.05s linear;
}

.coin{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px 0;
    width: 100%;
    transition: 0.125s linear;
}
.coinActive{
    transform: scale(1.6);
    padding: 15px 0;
}
.waitPlayerCircleWrapper{
    z-index: 9999999;
    overflow: visible;
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    top: 49%;
    /*transform: translateY(-50%);*/
}
.waitPlayerCircle{
    background-image: url("../../../assets/img/strelki.png");
    background-repeat: no-repeat;
    background-position: center center;
    margin-left: -59px;
    width: 100%;
    height: 100px;
    z-index: 9999999;
    transform: scale(0.8);
}

@media screen and (max-width: 1023px) {
    .waitPlayerCircle{
        margin-left: -9px;
    }
    .waitPlayerInner  {
        margin-top: -37px;
    }
}