#auth span, #authLogin span, #authRegistration span, #authLogin a, #authRegistration a {
    display: block;
    text-align: center;
    padding-top: 20px;
    font-size: 24px;
    margin-bottom: 15px;
}

#authLogin a, #authRegistration a {
    cursor: pointer;
    padding-bottom: 15px;
}

#auth, #authLogin, #authRegistration {
    margin-top: auto !important;
}

#auth, #authRegistration {
    padding: 20px;
}

#authLogin input[type="text"], #authLogin input[type="password"], #authRegistration input[type="text"], #authRegistration input[type="password"] {
    width:200px;
    border-radius: 3px;
    margin-bottom: 5px;
    border:1px solid #ccc;
    outline: none;
    height: 35px;
    display: inline-block;
    outline: none;
    padding-left: 5px;
}

#authLogin li, #authRegistration li {
    list-style: none;
    display: inline-block;
    vertical-align: top;
    width:200px;
}

#authRegistration {
    padding-bottom: 40px;
    height: 450px !important;
}

#authRegistration input[type="submit"], #authLogin input[type="submit"] {
    max-width: 100px;
    margin-left: 10px;
    margin-bottom: 0;
    text-align: center;
    color: #fff;
    font-family: MyriadPro-Bold;
    font-size: 13px;
    line-height: 49px;
    height: 49px;
    padding: 0 15px;
    letter-spacing: .33px;
    -webkit-box-shadow: 0 4px 18px rgba(235,180,47,.15);
    box-shadow: 0 4px 18px rgba(235,180,47,.15);
    -webkit-border-radius: 2px;
    border-radius: 2px;
    background-color: #ffb83a;
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    -webkit-transition: .3s;
    transition: .3s;
    border:none;
    margin:0 auto;
    display: block;
    margin-top: 10px;
}

#authLogin form, #authRegistration form {
    text-align: center;
}

.error-message {
    color:red;
    font-size: 13px;
    display: block;
    padding-bottom: 10px;

}

#auth ul {
    text-align: center;
    margin-bottom: 30px;
}

#auth ul li {
    display: inline-block;
}

#auth {
    height:250px !important;
}
.hide-icon img {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    -webkit-transition: .3s;
    transition: .3s;
}
