.bonus__slider--active {
    display: flex;
    margin-left: -80px;
}
.bonus{
    overflow: hidden;
}

.bonus-item__coins {
    display: block;
    color: #fff;
    font-family: MyriadPro;
    font-size: 9px;
    background-color: #afb0b1;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: 4.5px;
    padding-top: 2px;
    margin-bottom: 0
}

.bonus-item--green {
    background-color: #a5e2bf
}

.bonus-item--green .bonus-item__value {
    color: #15a656
}

.bonus-item--green .bonus-item__coins {
    background-color: #15a656
}

.bonus-item--yellow {
    background-color: #eedec2
}

.bonus-item--yellow .bonus-item__value {
    color: #e19612
}

.bonus-item--yellow .bonus-item__coins {
    background-color: #ffb83a
}

.bonus-item--blue {
    background-color: #b0c2ed
}

.bonus-item--blue .bonus-item__value {
    color: #3872f0
}

.bonus-item--blue .bonus-item__coins {
    background-color: #3265d4
}

.bonus-item--orange {
    background-color: #f9c99c
}

.bonus-item--orange .bonus-item__value {
    color: #e38542
}

.bonus-item--orange .bonus-item__coins {
    background-color: #e38542
}

.bonus-item--lose {
    background: #f2f5f7 url(../../../assets/img/icons/sad-smile.png) no-repeat
}