.game__participants-wrapper{
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    transition: .2s linear;
}
.game__participants-wrapper *, .game__participants--adaptive {
    list-style: none;
}
.game__participants--adaptive{
    display: none;
}


.game__participants-wrapper li img, .game__participants--adaptive li img {
    min-width: 60px;
}
@media screen and (max-width: 1440px) {
    .game__left {
        /*max-width: 100%;*/
    }

    .main__center--close .game__participants-wrapper {
        margin-right: -120%;
    }
}
@media screen and (max-width: 1400px) {
    .main__center--close .game__participants-wrapper {
        margin-right: -190%;
    }
}
@media screen and (max-width: 1380px) {
    .main__center--close .game__participants-wrapper {
        margin-right: -180%;
    }
}
@media screen and (max-width: 1023px){
    .game__participants-wrapper{
        display: none;
    }
    .game__left{
        /*max-width: 100%;*/
    }
    .game__center{
        position: relative;
    }
    .game__participants--adaptive{
        overflow: hidden;
        position: absolute;
        display: flex;
        top: 50%;
        transform: translate(-80%,-50%);
    }
}